/**
 * IMAGE CARD ELEMENT
 * Description: Calculates the height depending on screen sizes.
 * (a * b) / c
 * a: original height of the element
 * b: dynamic height of the element
 * c: original width of the element
*/
@mixin imageCardElementHeight($height) {
    height: (640px * $height) / 1140px;
}

/* TEXT SHADOWS FOR WHITE BACKGROUNDS */
@mixin textShadow() {
    text-shadow: 0 4px 3px rgba(0, 0, 0, .4),
    0 -3px 1px rgba(0, 0, 0, .1),
    0 6px 6px rgba(0, 0, 0, .1);
}

@mixin textShadow2() {
    text-shadow: 0 2px 3px rgba(0, 0, 0, .4),
    0 -3px 1px rgba(0, 0, 0, .1),
    0 6px 6px rgba(0, 0, 0, .1);
}