@import "variables";
@import "mixin";

/* MISC */
.bg-wrapper {
    height: inherit;
    width: inherit;
    opacity: 0.4;

    // Remove overlay on hover if exist.
    &:not(.hero-bg):hover {
        background-color: transparent !important;
        transition: all 1s;
        -webkit-transition: all 1s;
    }

}

/* DOM ELEMENTS */
h3 {
    color: $brand_black;
    font-size: 40px;
}

/* TOUT ELEMENT */
.tout {
    text-align: center;
    margin: 10px 0;
    > .title {
        font-family: $font-tradegothic;
        font-size: 40px;
        line-height: 40px;
        color: $brand_blue;
        text-transform: uppercase;
    }

    > .content {
        font-family: $font-maison_neue_book;
        font-size: 17px;
        line-height: 24px;
        font-weight: 300;
        margin: 25px 0 28px 0;
    }

    > .path {
        font-family: $font-maison_neue_demi;
        font-size: 17px;
        font-weight: 500;
        line-height: 24px;

    }
}

/* --- END OF TOUT ELEMENT --- */

/* IMAGE CARD ELEMENT */
.image-card-element {
    display: block;
    height: 640px;
    color: $white;
    margin: 5px 0;
    cursor: pointer;
    transition: all 1s;
    -webkit-transition: all 1s;

    // Puts a gray scale over the image when hover it.
    &.grayscale {
        -webkit-filter: grayscale(1) !important;
        &:hover {
            -webkit-filter: grayscale(0) !important;
        }
    }

    & > .wrapper {
        position: absolute;
        width: 97%;
        cursor: pointer;

        /* TEXT POSITIONS */
        &.left, &.center, &.right {
            &.top {
                top: 10%;
            }
            &.middle {
                top: 45%;
            }
            &.bottom {
                bottom: 10%;
            }
        }

        &.left {
            padding-left: 60px;
        }
        &.right {
            text-align: right;
            padding-right: 60px;
        }
        &.center {
            text-align: center;
        }
        /* --- END OF TEXT POSITIONS --- */

        & > .title {
            font-family: $font-tradegothic;
            font-size: 40px;
            line-height: 40px;
            color: $white !important;
            padding-bottom: 12px;
            text-transform: uppercase;
            @include textShadow();
        }

        & > .link-text {
            font-family: $font-maison_neue_light;
            display: inline;
            font-size: 17px;
            line-height: 24px;
            color: $white !important;
            padding-right: 14px;
            @include textShadow2();

            & > .i8-right {
                font-size: 34px;
                vertical-align: middle;
            }
        }
    }
}

/* --- END OF IMAGE CARD ELEMENT --- */

/* VIDEO ELEMENT */
.video-element {
    height: 792px;
    padding: 78px 0;

    & > #videoElementPlay {
        position: absolute;
        color: white;
        top: 45%;
        left: 47%;
        font-size: 70px;
        @include textShadow;
    }

    & > .wrapper {
        width: 100%;
        height: 100%;

        & > #videoElementImg {
            width: inherit;
            height: inherit;
        }

        & > #videoElementVideo {
            display: none;
            width: inherit;
            height: inherit;
            border: 0;
        }
    }
}

/* --- END OF VIDEO ELEMENT --- */

/* IMAGE OVERLAY ELEMENT */
.image-overlay {
    height: 470px;
    width: 360px;
    margin: 15px auto;
    display: block;

    & > img {
        position: absolute;
        width: inherit;
        height: inherit;
        opacity: .4;
    }

    &:hover {
        & > img {
            opacity: 1;
            transition: all 1s;
            -webkit-transition: all 1s;
        }
    }

    .wrapper {
        position: absolute;
        color: $white;
        bottom: 40px;
        padding-left: 32px;
        width: inherit;

        .title {
            font-family: $font-tradegothic;
            text-transform: uppercase;
            font-size: 39px;
            line-height: 40px;
            color: $white;
            @include textShadow();
        }

        .link-text {
            font-family: $font-maison_neue_light;
            @include textShadow2();

            .i8-right {
                font-size: 34px;
                vertical-align: middle;
            }
        }
    }

    &.no-overlay {
        img {
            opacity: 1;
        }
    }
}

/* --- END OF IMAGE OVERLAY ELEMENT --- */

/* --- CALL TO ACTION ELEMENT --- */
.call-to-action {
    background-color: $brand_blue;
    color: $white;
    text-align: center;
    height: 325px;
    & .wrapper {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        & > .title {
            font-family: $font-tradegothic;
            font-size: 32px;
            line-height: 32px;
            text-transform: uppercase;
            padding: 15px 0;
        }

        & > .content {
            font-family: $font-maison_neue_light;
        }

        & > .donation-options {
            font-family: $font-maison_neue_light;
            padding: 24px 0;

            & > li {
                list-style-type: none;
                display: inline-block;
                padding: 0 10px;

                & > label {
                    font-size: 12px;
                    padding: 5px 0;
                    cursor: pointer;
                    border: 2px solid $white;
                    border-radius: 20px;
                    width: 85px;

                }

                & > input {
                    display: none;

                    &:checked + label {
                        background-color: $white;
                        color: $brand_black;
                    }
                }
            }
        }

        & > .donate {
            width: 94px;
            background-color: $brand_black;

            &:hover, &:focus {
                color: $white;
            }
        }
    }
}

/* --- END OF CALL TO ACTION --- */

/* CALENDAR ELEMENT */

.fc-event-title, .fc-event-time {
    color: $white;
}

#calendarMobile {
    display: none;
}

/* --- END OF CALENDAR ELEMENT --- */

/* CMS element - Accordion */
.accordion {
    padding-left: 0;
    .dropdown {
        list-style-type: none;
        margin-bottom: 20px;
        a.dropdown-toggle {
            text-decoration: none;
            background-color: $grey-xl;
            color: $brand_black;
            display: block;
            padding: 20px 10px;
            vertical-align: middle;
            .glyphicon {
                float: right;
            }
            font-size: 20px;
        }
        li {
            list-style-type: disc;
        }
        span.glyphicon {
            color: $brand_blue;
            -webkit-transition-duration: 1.0s;
            -moz-transition-duration: 1.0s;
            -o-transition-duration: 1.0s;
            transition-duration: 1.0s;
            -webkit-transition-property: -webkit-transform;
            -moz-transition-property: -moz-transform;
            -o-transition-property: -o-transform;
            transition-property: transform;
            -webkit-transform: rotate(0deg);
            -moz-transform: rotate(0deg);
            -o-transform: rotate(0deg);
            transform: rotate(0deg);
            &:before {
                font-size: 28.56pt;
                line-height: 0.6;
            }
        }
        &.open {
            span.glyphicon {
                color: $white;
                -webkit-transition-duration: 1.0s;
                -moz-transition-duration: 1.0s;
                -o-transition-duration: 1.0s;
                transition-duration: 1.0s;
                -webkit-transition-property: -webkit-transform;
                -moz-transition-property: -moz-transform;
                -o-transition-property: -o-transform;
                transition-property: transform;
                -webkit-transform: rotate(45deg);
                -moz-transform: rotate(45deg);
                -o-transform: rotate(45deg);
                transform: rotate(45deg);
                &:before {
                }
            }
            a.dropdown-toggle {
                background-color: $brand_blue;
                color: $white;
                display: block;
            }
        }
    }
    .dropdown-menu {
        position: static;
        top: auto;
        left: auto;
        z-index: auto;
        float: none;
        border: none;
        box-shadow: none;
        background-color: $grey-xl;
        margin: 0;
        padding: 36px 90px;
        display: none;
    }
}

@media screen and (max-width: 760px) {
    .accordion .dropdown-menu {
        padding: 18px 45px;
    }
}

@media screen and (max-width: 512px) {
    .accordion .dropdown-menu {
        padding: 9px 22px;
    }
}

/* End CMS element  - Accordion */

/* CMS element - Hero */
.hero {
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: calc(100% / 1440 * 819);
    max-height: 840px;
    min-height: 624px;
    //padding: 135px 113px;
    cursor: default;

    &.home-hero {
        h1 {
            font-size: 70px;
            @include textShadow;
        }
        div {
            width: 526px;
            &.center {
                left: calc(50% - 263px);
            }
        }
    }

    h1 {
        font-family: $font-tradegothic;
        font-size: 60px;
        color: $white;
        line-height: 68px;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 0;
        @include textShadow;
    }
    h2 {
        border-top: solid;
        border-top-width: 7px;
        border-top-color: $brand_blue;
        color: $white;
        padding-top: 20px;
        font-size: 30px;
        @include textShadow;
    }
    a {
        &:active, &:hover, &:link, &:visited {
            color: $white;
            text-decoration: none;
            font-family: "sans-serif";
            font-weight: 500;
            font-size: 20px;
            line-height: 50px;
            background-color: $brand_blue;
            width: 230px;
            //height: 50px;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;
            display: block;
            text-align: center;
            padding: 0;
            margin-top: 61px;
        }
    }
    div {
        width: 770px;
        position: absolute;
        &.bottom {
            bottom: 130px;
        }
        &.top {
            top: 135px;
        }
        &.middle {
            top: calc(50% - 226px);
        }
        &.left {
            left: 113px;
        }
        &.right {
            right: 113px;
            text-align: right;
            a {
                float: right;
            }
        }
        &.center {
            left: calc(50% - 385px);
            text-align: center;
            a {
                margin-left: auto;
                margin-right: auto;
            }
        }
    }

    & > .bg-wrapper {
        height: 96% !important;
        width: inherit !important;
    }
}

@media screen and (min-width: 2560px) {
    .hero {
        div {
            &.bottom {
                bottom: 70px;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .hero {
        div {
            &.bottom {
                bottom: 70px;
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .hero {
        background-attachment: local;
        //padding: 67px 56px;

        &.home-hero {
            div {
                width: 363px;
                &.center {
                    left: calc(50% - 181px);
                }
            }
        }
        h1 {
            font-size: 60px;
            line-height: 60px;
        }
        h2 {
            border-top-width: 6px;
            font-size: 27px;
        }
        div {
            width: 600px;
            &.center {
                left: calc(50% - 300px);
            }
            &.right {
                a {
                    margin-left: auto;
                    margin-right: auto;
                }
            }

        }
    }
}

@media screen and (max-width: 999px) {
    .hero {
        h1 {
            font-size: 50px;
            line-height: 50px;
        }
        h2 {
            border-top-width: 5px;
            font-size: 24px;
        }
        div {
            &.right {
                right: 85px;
            }
            &.left {
                left: 85px;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .hero {
        min-height: 624px;
        padding-left: 0;
        padding-right: 0;

        &.home-hero {
            //width: 316px;
            &.center {
                left: calc(50% - 158px);
            }
        }
        h1 {
            font-size: 40px;
            line-height: 40px;
            margin-left: auto;
            margin-right: auto;
        }
        h2 {
            border-top-width: 4px;
            font-size: 20px;
        }
        a {
            margin-left: auto;
            margin-right: auto;
        }
        div {
            width: 380px;
            &.center {
                left: calc(50% - 190px);
            }
            &.right {
                right: 10px;
            }
            &.left {
                left: 10px;
            }
        }
    }
}

@media screen and (max-width: 400px) {
    .hero {
        div {
            text-align: center;
            &.center {
                left: 0;
                width: 100%;
            }
            &.right {
                text-align: center;
                right: 0;
                width: 100%;
                a {
                    float: none;
                }
            }
            &.left {
                left: 0;
                width: 100%;
            }
        }
    }
}

/* End CMS element - Hero */

/* CMS element - Curve Down */
#curveDown {
    margin-top: -50%;
    width: 100%;
    height: 120px;
    padding-bottom: 14px;
    path {
        fill: $white;
        stroke: $white;
    }
}

@media screen and (min-width: 2560px) {
    #curveDown {
        height: 60px;
    }
}

@media screen and (max-width: 768px) {
    #curveDown {
        height: 60px;
    }
}

/* End CMS element - Curve Down*/

/* CMS element - Curve Up */
#curveUp {
    margin-top: -250px;
    width: 100%;
    height: 120px;
    padding-bottom: 12px;
    z-index: 10;
    position: relative;
    path {
        fill: $white;
        stroke: $white;
    }
}

@media screen and (min-width: 2560px) {
    #curveUp {
        height: 60px;
    }
}

@media screen and (max-width: 768px) {
    #curveUp {
        height: 60px;
    }
}

/* End CMS element - Curve Up*/

/* CMS element - staff bio */
$font-size: 14px;
$line-height: 20.3px;
$lines-to-show: 3;

.staff-bio {
    padding: 30px 0;
    div {
        font-family: $font-maison_neue_medium;
        padding: 0;
        &:first-child {
            padding-right: 30px;
        }
        &:last-child {
            padding-left: 0;
        }
    }
    img {
        width: 100%;
        max-height: 100%;
    }
    h4.name {
        font-size: 20px;
        font-weight: 500;
        margin: 0;
        line-height: 29px;
    }
    h5.title {
        font-family: $font-maison_neue_book;
        font-size: 17px;
        font-weight: 300;
        margin: 0 0 16px;
        line-height: 24px;
    }
    a.show-bio {
        display: none;
        text-decoration: none;
        &:hover, &:active, &:link, &:visited {
            text-decoration: none;
            color: $brand_blue;
        }
    }
}

@media screen and (max-width: 768px) {
    .staff-bio {
        div {
            &:first-child {
                padding-right: 0px;
            }
            &:last-child {
                padding-left: 10px;
            }
            p {
                display: none;
                &:first-child {
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    display: block;
                    display: -webkit-box;
                    height: 62px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    margin: 0;
                }
            }
            h4.name {
                margin-top: 20px;
                font-size: 17px;
                line-height: 24px;
            }
            h5.title {
                font-size: 15px;
                line-height: 22px;
            }
            a.show-bio {
                margin-top: 20px;
                display: inline-block;
                font-family: $font-maison_neue_demi;
            }
        }
        div.open {
            a.show-bio {
                display: none;
            }
            p {
                display: block;
                &:first-child {
                    -webkit-line-clamp: initial;
                    display: block;
                    height: inherit;
                    overflow: inherit;
                    margin: 0 0 10px;
                }

            }
        }

    }
}

@media screen and (max-width: 414px) {
    .staff-bio {
        div {
            display: block;
            width: 100%;
            padding: 0;
            img {
                width: 80%;
                margin-left: auto;
                margin-right: auto;
                display: block;
            }
        }
    }
}

/* End CMS element - staff bio */
